<template>
  <div class="tanker-detail">
    <div style="padding-top:15px;" class="container-fluid">
      <div class="row clearfix">
        <!-- load-status组件 -->
        <div class="col-sm-6"><load-status :lzn="lzn.tankerDetails" :ls="bayItem.bay_load[0].load_status" :baycode="$route.params.tdid" :props="bayItem.bay_interlocks"></load-status></div>
        <!-- load-detail组件 -->
        <div class="col-sm-6"><load-detail :lzn="lzn.tankerDetails" :props="bayItem.bay_load[0]" style="float:right;"></load-detail></div>
      </div>
    </div>

    <!-- 发油台处于loading或active状态则显示车辆 -->
    <div class="cmpt" :style="{'background-image':(isLoading||isActive)?undefined:'none'}">
      <!-- 发油台处于loading或active状态则显示油仓表头 -->
      <div v-if="isLoading||isActive" class="cmptth">
        <div>{{lzn.tankerDetails.lbl_cmptNo}}</div>
        <div>{{lzn.tankerDetails.lbl_preset}}</div>
        <div>{{lzn.tankerDetails.lbl_qty_obs}}</div>
        <div>{{lzn.tankerDetails.lbl_prodName}}</div>
      </div>
      <!-- 发油台处于loading或active状态则显示油仓数据 -->
      <div v-if="isLoading||isActive" class="cmpt-container clearfix">
        <cmpt class="fl" v-for="i in cmptPresence" :key="i.cmpt_no" :props="i"></cmpt>
      </div>
      <!-- 发油台处于loading或active状态则显示油仓切换按钮 -->
      <img v-if="isLoading||isActive" @click="sl" src="../assets/left.png" style="position:absolute;left:220px;top:106px;width:16px;height:71px;cursor:pointer;" alt="left">
      <img v-if="isLoading||isActive" @click="sr" src="../assets/left.png" class="c-right" style="position:absolute;left:874px;top:106px;width:16px;height:71px;cursor:pointer;" alt="right">
      <!-- arm区域 -->
      <div class="arm-wrap">
        <!-- arm列表表头 -->
        <div class="armth">
          <div>{{lzn.tankerDetails.lbl_armNo}}:</div>
          <div>{{lzn.tankerDetails.lbl_temp}}:</div>
        </div>
        <!-- arm管道和管道数据显示区域 -->
        <div v-if="isLoading||isActive" class="arm-container">
          <div class="arm-c-f clearfix">
            <div v-for="i in arms">
              <div v-if="i.arm_number!=undefined" class="hg">
                <div :class="((i.arm_status===true||i.arm_status=='L')&&isLoading)?'arm-load':''"></div>
              </div>
              <div class="armv">{{i.arm_number}}</div>
              <div class="armv">{{i.arm_temperature}}</div>
            </div>
          </div>
        </div>
        <!-- 发油台背景区域 -->
        <div class="arm-box"> 
          <div style="width:130px;height:100%;border-right:1px solid #000;float:left;">{{bayItem.bay_name}}</div>
          <div v-if="!isLoading&&!isActive" class="allarm">
            <div class="arm-c-f clearfix" :style="{width:arml*cmptw+'px',transform:'translateX('+armtx+'px)'}">
              <div v-for="i in bayItem.bay_arms">
                <div class="armv">{{i.arm_number}}</div>
                <div class="armv">{{i.arm_temperature}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="ft">
      <sbutton @click.native="print"><span>{{lzn.tankerDetails.btn_prtScn}}</span></sbutton><sbutton @click.native="showBD"><span>{{lzn.tankerDetails.btn_bayDetails}}</span></sbutton><sbutton @click.native="showO"><span>{{lzn.tankerDetails.btn_overview}}</span></sbutton>
    </div>
  </div>
</template>

<script>
import Sbutton from './Sbutton'
import LoadDetail from './LoadDetail'
import LoadStatus from './LoadStatus'
import Cmpt from './Cmpt'
import html2canvas from 'html2canvas'

export default {
  name: 'tankerdetail',
  props: {
    lzn:Object
  },
  components: {
    Sbutton,LoadDetail,LoadStatus,Cmpt
  },
  data () {
    return {
      tx: 0,//油仓切换标记
      armtx:0,//没有发油、arm数量超过6个时的动画的translateX
      st:null,//bayItem实时刷新定时器
      cmptw:84,//油仓宽度
      bayItem:{
        bay_tanker:[{
          tnkr_cmpts:[]
        }],
        bay_load:[{}],
        bay_arms:[]
      },
      cl:0,//油仓数量
      arml:0,//arm数量
      armst:null//没有发油、arm数量超过6个时的动画定时器
    }
  },
  created(){
    document.title = this.lzn.tankerDetails.title; 
    //没有发油、arm数量超过6个时，动画显示arms
    this.$watch(function () {
      return !this.isLoading&&!this.isActive&&this.arml>6;
    },function (newVal, oldVal) {
      //停止动画并回到初始化位置
      this.armtx = 0;
      clearInterval(this.armst);
      //如果未发油且arm数量大于6
      if(newVal){
        //当前页
        let page=0;
        //开始动画
        this.armst = setInterval(()=>{
          //没到最后一页
          if(++page<Math.ceil(this.arml/6)){
            this.armtx-=504;
          }else{
            page = 0;
            this.armtx = 0;
          }
        },2000);
      }
    });
    this.getBayItem();
  },
  computed:{
    //找到油仓对应的arm，没找到就对应一个空对象
    arms(){
      let r = [];
      this.bayItem.bay_tanker[0].tnkr_cmpts.forEach((i)=>{
        let t = this.bayItem.bay_arms.find((j)=>{
          return i.arm_number==j.arm_number;
        });
        if(t){
          r.push(t);
        }else{
          r.push({});
        }
      });
      //tx控制当前显示哪些数据
      return r.slice(this.tx,this.tx+6);
    },
    //发油中
    isLoading(){
      return this.bayItem.bay_load[0].load_status_id == 'L';
    },
    //active中
    isActive(){
      return this.bayItem.bay_load[0].load_status_id == 'A';
    },
    //显示的油仓数据
    cmptPresence(){
      return this.bayItem.bay_tanker[0].tnkr_cmpts.slice(this.tx,this.tx+6);
    }
  },
  methods:{
    showO(){
      this.$router.back();
    },
    showBD(){
      this.$router.push({ path: '/baydetail/'+this.$route.params.tdid });
    },
    //油仓左移
    sl(){
      if(this.cl>6&&this.tx>0){
        this.tx--;
      }
    },
    //油仓右移
    sr(){
      if(this.cl>6&&this.tx+6<this.cl){
        this.tx++;
      }
    },
    //获取bayItem初始化数据
    getBayItem(){
      this.$http.get(bayViewConfig.apis.bayItem+'&bay_code='+this.$route.params.tdid).then((r)=>{
        if(r.data){
          this.bayItem = r.data[0];
          this.cl = this.bayItem.bay_tanker[0].tnkr_cmpts.length;
          this.arml = this.bayItem.bay_arms.length;
          this.getRTBayItem();
        }
      });
    },
    //获取bayItem实时数据
    getRTBayItem(){
      this.$http.get(bayViewConfig.apis.bayItemRT+'&bay_code='+this.$route.params.tdid).then((r)=>{
        if(r.data){
          //合并实时数据,更新cl,arml
          Object.assign(this.bayItem, r.data[0]);
          this.cl = this.bayItem.bay_tanker[0].tnkr_cmpts.length;
          this.arml = this.bayItem.bay_arms.length;
        }
      }).finally(()=>{
          //ajax异步回调时如果路由已经改变，则取消回调
          if(!this.$router.currentRoute.path.startsWith('/tankerdetail')){
            return;
          }
          this.st = setTimeout(()=>{
            this.getRTBayItem();
          },bayViewConfig.refreshFrequency);
      });
    },
    print(){
      html2canvas(document.body).then((canvas)=> {
        this.$emit('ps',canvas,'TankerDetail');
      })
    }
  },
  beforeDestroy(){
    //组件销毁前清除定时器
    clearTimeout(this.st);
    clearInterval(this.armst);
  }
}
</script>

<style lang="scss">
.tanker-detail{
  width:980px;
  margin:0 auto;
  background-color:#ffffff;
  padding-bottom:66px;
}
.cmpt{
  clear:both;
  width:940px;
  background-image:url(../assets/Tanker.png);
  background-repeat:no-repeat;
  position:relative;
  margin:0 auto;
  margin-top:40px;
  height:420px;
}
.arm-wrap{
  width:100%;
  position:absolute;
  z-index:2;
  height:210px;
  top:195px;
  box-sizing:border-box;
  .armth{
    position:absolute;
    z-index:3;
    background:linear-gradient(to bottom,#ffffff, #f0f0f0);
    top:152px;
    left:240px;
    div{
      padding:3px 4px;
    }
  }
  .allarm{
    position:absolute;
    width:504px;
    left:366px;
    overflow:hidden;
  }
  .arm-box{
    width:100%;
    position:absolute;
    height:60px;
    z-index:2;
    top:150px;
    border:1px solid #000000;
    background:linear-gradient(to bottom,#ffffff, #f0f0f0);
  }
}
.cmpt-container{
  width:504px;
  position:absolute;
  top:86px;
  left:366px;
  z-index:2;
  overflow:hidden;
}
.arm-container{
  width:504px;
  z-index:3;
  position:absolute;
  left:366px;
  overflow:hidden;
}
.arm-c-f{
    &>div{
    width:84px;
    float:left;
    &>div.armv{
      padding:4px;
      text-align:center;
    }
  }
}
.cmptth{
  width:126px;
  position:absolute;
  top:86px;
  left:240px;
  z-index:3;
  border:1px solid #fff;
  border-top:none;
  div{
      white-space:nowrap;
      height:26px;
      line-height:26px;
      padding:0 2px;
      border-top:1px solid #fff;
  }
}
.c-right{
  transform: rotate(180deg);
}
@media (max-width: 768px) {
  .load-detail{
    margin:0 auto;
    margin-top:15px;
    float:none !important;
  }
  .load-status{
    margin:0 auto;
  }
}
@keyframes armload
{
  from {transform: translateY(0);}
  to {transform: translateY(-40px);}
}
.hg{
  height:150px;
  overflow:hidden;
  div{
    width:16px;
    border:2px solid #666;
    border-top:none;
    border-bottom:none;
    margin:0 auto;
    height:200px;
    background-color:#ccc;
  }
  .arm-load{
    background-image:url(../assets/ts.png);
    animation:armload 1s linear infinite;
  }
}
.maq-c{
  animation:maq 2s linear infinite;
}
</style>



