<template>
    <div class="cmpt-item">  
      <!-- load进度表示 -->
      <div class="cmpt-process" :style="{height:process+'%','background-color':props.prod_color}">
        
      </div>
      <!-- 数据区域 -->
      <div class="cmpt-data">
        <div>{{props.cmpt_no}}</div>
        <div>{{preset}}</div>
        <div>{{loaded}}</div>
        <div :title="props.prod_name">{{props.prod_name}}</div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'cmpt',
  props: {
    props: Object
  },
  data () {
    return {

    }
  },
  computed:{
    //根据不同的计量单位返回不同的预设值
    preset(){
      if(this.props.prod_unit==5){
        return this.props.cmpt_amb_schd;
      }
      if(this.props.prod_unit==11){
        return this.props.cmpt_std_schd;
      }
      if(this.props.prod_unit==17){
        return this.props.cmpt_mass_schd;
      }
      return '';
    },
    //根据不同的计量单位返回不同的已加油值
    loaded(){
      if(this.props.prod_unit==5){
        return this.props.cmpt_amb_load;
      }
      if(this.props.prod_unit==11){
        return this.props.cmpt_std_load;
      }
      if(this.props.prod_unit==17){
        return this.props.cmpt_mass_load;
      }
      return '';
    },
    //返回load进度(保留2位小数)
    process(){
      return Math.round(this.loaded/this.preset*10000)/100;
    }
  },
  methods:{

  }
}
</script>

<style lang="scss">
  .cmpt-item{
    width:84px;
    box-sizing:border-box;
    background:linear-gradient(to top,#979797, #a4a4a4);
    position:relative;
  }
  .cmpt-process{
    position:absolute;
    bottom:0;
    width:100%;
    z-index:1;
  }
  .cmpt-data{
    border:1px solid #fff;
    border-top:none;
    position:relative;
    z-index:2;
    div{
      white-space:nowrap;
      overflow:hidden;
      height:26px;
      line-height:26px;
      padding:0 2px;
      border-top:1px solid #fff;
    }
  }
</style>



