<template>
  <!-- 一个arm对应的所有管道 -->
  <div class="pipe-node">
    <div class="pca">
      <!-- 取主管道productName -->
      <div class="pn"><div :title="props[0]?props[0].cmpt_prod_name:''" class="pca-d">{{props[0]?props[0].cmpt_prod_name:''}}</div></div>
      <!-- 取主管道cmpt_no -->
      <div class="cn"><div class="pca-d">{{props[0]?props[0].cmpt_no:''}}</div></div>
      <!-- 取主管道arm_number -->
      <div class="arm-no">arm {{props[0]?props[0].arm_number:''}}</div>
    </div>
    <!-- 管道 -->
    <div v-for="(item,i) in mainPipeNode" :style="{'z-index':i}" class="pipe">
      <!-- 当调和类型为SEQUENTIAL且是副管道时设置管道宽度为'444px'，否则为100% -->
      <div class="line" :style="{width:(item.arm_blendtype_code=='SEQUENTIAL'&&item.stream_seq>1)?'444px':''}">
        <!-- 为副管道时设置右边距 -->
        <div :style="{'margin-right':item.stream_seq>1?'200px':''}"></div>
      </div>
      <!-- 为副管道时,显示与其他管道的连接管道,根据调和类型设置左右间距 -->
      <div v-if="item.stream_seq>1" :style="{left:item.arm_blendtype_code=='SEQUENTIAL'?'240px':'',right:item.arm_blendtype_code=='SEQUENTIAL'?'auto':''}" class="v-line"></div>
      <!-- 如果调和类型不是SEQUENTIAL或者管道为主管道，显示流量计读数，
           根据管道类型（主|副）调整位置 -->
      <div v-if="!(item.arm_blendtype_code=='SEQUENTIAL'&&item.stream_seq>1)" class="meter-value" :style="{right:item.stream_seq==1&&item.arm_blendtype_code!='RATIO'?'55px':'250px'}">{{item.meter_totalizer}}</div>
      <img v-if="!(item.arm_blendtype_code=='SEQUENTIAL'&&item.stream_seq>1)" class="meter" :style="{right:item.stream_seq==1&&item.arm_blendtype_code!='RATIO'?'100px':'295px'}" src="../assets/meter.png" alt="meter">
      <!-- 如果调和类型是SEQUENTIAL,显示阀门 -->
      <img v-if="item.arm_blendtype_code=='SEQUENTIAL'" class="valve" src="../assets/valve.png" alt="valve">
      <!-- 如果为主管道且为加锁状态,显示锁图片 -->
      <img v-if="item.stream_seq==1&&item.arm_lock=='Y'" class="lock" src="../assets/lock.png" alt="lock">
      <!-- 油罐名称 -->
      <div class="tbox">{{item.tank_name}}</div>
      <!-- 油品名称 -->
      <div :title="item.base_name" class="p-name">{{item.base_name}}</div>
      <!-- 如果调和类型不是SEQUENTIAL或者管道为主管道,显示添加剂 -->
      <div v-if="!(item.arm_blendtype_code=='SEQUENTIAL'&&item.stream_seq>1)" class="injector">
        <!-- 添加剂数量 -->
        <div>{{item.arm_injectors}}</div>
        <!-- 添加剂状态 -->
        <div :class="getInjectorColor(item)?'ict':'icf'"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'pipeNode',
  props: {
    props: Array//一个arm对应的所有管道数组
  },
  data () {
    return {

    }
  },
  computed:{
    //筛选管道（去除添加剂线路）并按stream_seq排序，主管道在最前面
    mainPipeNode(){
      let r = [];
      this.props.forEach((i)=>{
        if(i.meter_code!=null&&i.injector_code==null){
          r.push(i);
        }
      });
      r.sort(function(a, b) {
        return a.stream_seq - b.stream_seq;
      });
      return r;
    }
  },
  methods:{
/*    getMeterValue(pipeNode){
      if(pipeNode.meter_qtytype==0){
        return pipeNode.meter_avol_total;
      }else if(pipeNode.meter_qtytype==1){
        return pipeNode.meter_cvol_total;
      }else if(pipeNode.meter_qtytype==2){
        return pipeNode.meter_mass_total;
      }else{
        return '';
      }
    },*/
    /* 获取添加剂状态（只要添加剂线路中的其中一条处于运行状态就返回true） */
    getInjectorColor(pipeNode){
      for(let i=0;i<this.props.length;i++){
        let t = this.props[i];
        if(t.injector_code!=null&&t.meter_code==pipeNode.meter_code&&t.stream_seq==pipeNode.stream_seq&&t.injector_status!=0){
          return true;
        }
      }
      return false;
    }
  }
}
</script>

<style lang="scss">
.pipe-node{
  .pca{
    width:400px;
    height:100px;
    line-height:100px;
    float:right;
    div{
      box-sizing:border-box;
    }
    .pca-d{
      white-space:nowrap;
      display:inline-block;
      padding:0 5px;
      vertical-align:middle;
      width:100%;
      height:32px;
      line-height:32px;
      border:1px solid #000000;
      background-color:#ffffff;
    }
  }
  .pn{
    width:200px;
    height:100%;
    float:right;
    padding:0 5px;
  }
  .cn{
    width:130px;
    height:100%;
    float:right;
    padding:0 5px;
  }
  .arm-no{
    width:60px;
    height:100%;
    float:right;
    padding:0 5px;
  }
  .pipe{
    margin-right:400px;
    height:100px;
    position:relative;
    .line{
      position:absolute;
      width:100%;
      top:48px;
      left:0;
      &>div{
        height:4px;
        background-color:#ce9e2a;
        margin-left:120px;
      }
    }
    .v-line{
      position:absolute;
      top:-50px;
      right:200px;
      height:100px;
      width:4px;
      background-color:#ce9e2a;
      z-index:2;
    }
    .meter{
      width:30px;
      height:30px;
      position:absolute;
      top:35px;
    }
    .meter-value{
      width:120px;
      text-align:center;
      height:30px;
      line-height:30px;
      position:absolute;
      border:1px solid #000;
      top:0;
    }
    .tbox{
      position:absolute;
      top:35px;
      left:30px;
      width:80px;
      text-align:center;
      height:30px;
      line-height:30px;
      border:1px solid #000;
    }
    .lock{
      position:absolute;
      top:18px;
      right:15px;
      width:24px;
      height:24px;
      z-index:9;
    }
    .valve{
      position:absolute;
      top:20px;
      left:155px;
      width:48px;
      height:42px;
      z-index:9;
    }
    .p-name{
      position:absolute;
      top:70px;
      left:10px;
      width:120px;
      font-size:12px;
      text-align:center;
    }
    .injector{
      width:40px;
      height:20px;
      position:absolute;
      top:15px;
      left:250px;
      border:1px solid #000;
      display:flex;
      text-align:center;
      div{
        flex:1;
        &:first-child{
          border-right:1px solid #000;
        }
      }
    }
    .ict{
      background-color:#00a249;
    }
    .icf{
      background-color:#bbb;
    }
  }
}
</style>



