  </div>
</template>

<script>
export default {
  name: 'loaddetail',
  props: {
    props: Object,
    lzn:Object
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="scss">
  .load-detail{
    background:linear-gradient(to bottom,#a6a6a6, #d6d6d6);
    border:1px solid #626262;
    box-shadow:1px 1px 2px rgba(0,0,0,.4);
    padding:5px;
    width:320px;
    .ldr{
      margin-top:10px;
      & > div{
        width:50%;
        box-sizing: border-box;
        line-height:24px;
        height:26px;
        padding:0 8px;
        float:left;
        &:first-child{
          text-align:right;
        }
      }
      .ldr-value{
        border:1px solid #000;
        background-color:#ffffff;
		white-space:nowrap;
		overflow:hidden;
      }
      .ldr-label{
        white-space:nowrap;
      }
    }
  }
</style>



