<template>
  <div id="app">
    <router-view :lzn="applzn" @startCheckLogin="checkLoginStatus" @ps="print"></router-view>
    <!-- 屏幕截图显示区域 -->
    <div v-show="psWrapShow" @click.self="hide" class="ps-wrap">
      <a :href="imgDataUrl" :download="fn+'.png'">
        <img style="vertical-align:middle;width:980px;" :src="imgDataUrl" alt="" title="点击保存">
      </a>
    </div>
  </div>
</template>

<script>

export default {
  name: 'app',
  data(){
    return {
        st:null,
        imgDataUrl:'',//截图url
        psWrapShow:false,//截图区域是否显示
        fn:'',//保存截图时的文件名
        applzn:{//本地化默认数据
            "en_login":{
              "title": "Login",
              "usernamepl":"username",
              "pwdpl":"password",
              "btn_login":"Login",
              "clear_title":"clear",
              "loginFailedMsg":"Login Failed"
            },
            "cn_login":{
              "title": "登录",
              "usernamepl":"用户名",
              "pwdpl":"密码",
              "btn_login":"登录",
              "clear_title":"清除",
              "loginFailedMsg":"登录失败!"
            },
            "overview": {
              "title": "Overview",
              "select_bayType": "Loading Bay Type",
              "select_bayCode": "Loading Bay Code",
              "select_baseProd": "Base Product",
              "btn_expData": "Export Data",
              "btn_prtScn": "Print Screen",
              "lbl_esd": "EMERGENCY SHUT DOWN",
              "lbl_tnkrID": "TANKER ID",
              "lbl_loadID": "LOAD ID"
            },
            "tankerDetails": {
              "title": "Tanker Details",
              "btn_prtScn": "Print Screen",
              "btn_bayDetails": "Bay Details",
              "btn_overview": "BayView Overview",
              "lbl_siteESD": "SITE EMERGENCY STOP",
              "lbl_bayESD": "BAY EMERGENCY STOP",
              "lbl_earth_overfill": "EARTH \/ OVERFILL",
              "lbl_vapour": "VAPOUR",
              "lbl_overfill": "OVERFILL",
              "lbl_earth": "EARTH",
              "lbl_connected": "CONNECTED",
              "lbl_not_connected": "NOT CONNECTED",
              "lbl_unparked": "UNPARKED",
              "lbl_parked": "PARKED",
              "lbl_trans_switch": "TRANS SWITCH",
              "lbl_drum_in_position": "DRUM IN POSITION",
              "lbl_spear_down": "SPEAR DOWN",
              "lbl_no_type": "NO TYPE",
              "lbl_deadman": "DEADMAN",
              "lbl_issteady": "IS STEADY",
              "lbl_curBayCode":"Bay Code",
              "lbl_loadStat": "Load Status",
              "lbl_loadDetails": "LOAD DETAILS",
              "lbl_cmpyCode": "Company Code",
              "lbl_loadNo": "Load Number",
              "lbl_operName": "Operator Name",
              "lbl_tnkrCode": "Tanker Code",
              "lbl_cmptNo": "Cmpt No.",
              "lbl_preset": "Preset",
              "lbl_qty_obs": "Observed Qty",
              "lbl_prodName": "Product Name",
              "lbl_armNo": "Arm Number",
              "lbl_temp": "Temperature"
            },
            "bayDetails": {
              "title": "Bay Details",
              "btn_prtScn": "Print Screen",
              "btn_tnkrDetails": "Tanker Details",
              "btn_overview": "BayView Overview",
              "lbl_siteESD": "SITE EMERGENCY STOP",
              "lbl_bayESD": "BAY EMERGENCY STOP",
              "lbl_earth_overfill": "EARTH \/ OVERFILL",
              "lbl_vapour": "VAPOUR",
              "lbl_overfill": "OVERFILL",
              "lbl_earth": "EARTH",
              "lbl_connected": "CONNECTED",
              "lbl_not_connected": "NOT CONNECTED",
              "lbl_unparked": "UNPARKED",
              "lbl_parked": "PARKED",
              "lbl_trans_switch": "TRANS SWITCH",
              "lbl_drum_in_position": "DRUM IN POSITION",
              "lbl_spear_down": "SPEAR DOWN",
              "lbl_no_type": "NO TYPE",
              "lbl_deadman": "DEADMAN",
              "lbl_issteady": "IS STEADY",
              "lbl_curBayCode":"Bay Code",
              "lbl_loadStat": "Load Status",
              "lbl_loadDetails": "LOAD DETAILS",
              "lbl_cmpyCode": "Company Code",
              "lbl_loadNo": "Load Number",
              "lbl_operName": "Operator Name",
              "lbl_tnkrCode": "Tanker Code",
              "lbl_tnkrDetails": "TANKER DETAILS",
              "lbl_armNo": "Arm No.",
              "lbl_cmptNo": "Cmpt. No.",
              "lbl_prodName": "Product Name"
            }
      }
    }
  },
  created(){
    this.getViewTxt();
    this.getInterlockConfig();
    this.checkLoginStatus();
  },
  methods:{
    //屏幕截图
    print(canvas,fname){
      this.imgDataUrl = canvas.toDataURL("image/png");
      this.fn = fname;
      this.psWrapShow = true;
    },
    //隐藏截图
    hide(){
      this.psWrapShow = false;
    },
    //获取本地化数据
    getViewTxt(){
      this.$http.get(bayViewConfig.apis.viewtxt).then((r)=>{
        if(r.data){
          this.applzn = r.data;
        }
      });
    },
    //get interlock configuration
    getInterlockConfig(){
      this.$http.get(bayViewConfig.apis.bayInterlockCfg).then((r)=>{
        if(r.data){
          bayViewConfig.interlockCfg = r.data;
        }
      });
    },
    //获取登录状态
    checkLoginStatus(){
      this.$http.get(bayViewConfig.apis.userSession).then((r)=>{
        if(r.data){
          this.$router.auth=r.data.VALID_SESSION;
          if(this.$route.path!='/login'){
            if(!r.data.VALID_SESSION){
              this.$router.push({ path: '/login', query: { redirect: this.$route.fullPath }});
            }else{
              this.st = setTimeout(()=>{
                this.checkLoginStatus();
              },2000);
            }
          }
        }
      }).catch(()=>{
        if(this.$route.path!='/login'){
          this.st = setTimeout(()=>{
            this.checkLoginStatus();
          },2000);
        }
      });
    }
  },
  beforeDestroy(){
    //组件销毁前，移除定时器
    clearTimeout(this.st);
  }
}
</script>

<style lang="scss">
html{
  height:100%;
}
#app{
  height:100%;
}
body {
  -webkit-font-smoothing: antialiased;
  background-color:#ffffff;
  height:100%;
}
.clearfix{
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}
.ft{
  position:fixed;
  width:100%;
  left:0;
  z-index:999;
  bottom:0;
  padding:10px 0 20px 0;
  background:#ffffff;
  text-align:right;
  button{
    margin-right:10px;
  }
}
.fl{
  float:left;
}
.ps-wrap{
  position: fixed;
  z-index: 99999;
  display: block;
  top: 0px;
  left: 0px;
  background-color: #000;
  text-align: center;
  width: 100%;
  height: 100%;
  line-height:974px;
}
</style>



