<template>
<div style="height:100%;">
  <div class="wrap">
    <section class="login-form">
      <div class="row clearfix">
        <!-- 用户名框 -->
        <div class="col-sm-10 col-sm-offset-1">
          <div class="input-group">
              <div class="input-group-addon" :style="{'border-color':userError?'rgba(173, 33, 33, 0.45)':''}"><div class="icon icon-user"></div></div>
              <input type="text" ref="userInput" v-model.trim="username" class="login-form-control input-lg" :placeholder="viewText.usernamepl" autocomplete="off" :style="{'border-color':userError?'rgba(173, 33, 33, 0.45)':''}">
              <div @click="clearUser" class="icon icon-clear" :title="viewText.clear_title"></div>
          </div>
        </div>
        <!-- 密码框 -->
        <div class="col-sm-10 col-sm-offset-1">
          <div class="input-group">
              <div class="input-group-addon" :style="{'border-color':pwdError?'rgba(173, 33, 33, 0.45)':''}"><div class="icon icon-pwd"></div></div>
              <input type="password" ref="pwdInput" v-model.trim="password" class="login-form-control input-lg" :placeholder="viewText.pwdpl" autocomplete = "off" :style="{'border-color':pwdError?'rgba(173, 33, 33, 0.45)':''}">
              <div @click="clearPwd" class="icon icon-clear" :title="viewText.clear_title"></div>
          </div>   
        </div>
        <!-- 登录按钮 -->
        <div class="col-sm-10 col-sm-offset-1">
          <button type="button" @click="submit" class="login-btn"><span>{{viewText.btn_login}}</span></button>
        </div>
      </div>
    </section>
  </div>
  <!-- 语言切换 -->
  <div @click="selectLang" class="lang-select">
    <span :class="lang=='ENG'?'':'c-lang'">中文</span> / <span :class="lang=='ENG'?'c-lang':''">En</span>
  </div>
  <!-- 登录失败提示消息 -->
  <div ref="errorMsg" :class="['login-failed',trans,fadeout]">{{viewText.loginFailedMsg}}</div>
</div>
</template>

<script>
import {md,util} from "node-forge"
import Blowfish from 'egoroof-blowfish'

export default {
  name: 'login',
  props: {
    lzn:Object
  },
  data () {
    return {
        username:'',
        password:'',
        lang:'ENG',
        fadeout:'',
        trans:'',
        userError:false,
        pwdError:false     
    }
  },
  computed:{
    viewText(){
      if(this.lang=='CHN'){
        document.title = this.lzn.cn_login.title;
        return this.lzn.cn_login;
      }else{
        document.title = this.lzn.en_login.title;
        return this.lzn.en_login;
      }      
    }
  },
  methods:{
    clearUser(){
      this.username = '';
      this.$refs.userInput.focus();
    },
    clearPwd(){
      this.password = '';
      this.$refs.pwdInput.focus();
    },
    submit(){
      //用户名为空,return
      if(this.username==''){
        this.userError = true;
        this.$refs.userInput.focus();
        return;
      }
      //密码为空,return
      if(this.password==''){
        this.pwdError = true;
        this.$refs.pwdInput.focus();
        return;
      }
      //post登录
      this.$http.post(bayViewConfig.apis.userLogin,this.qs({lang:this.lang,user:this.username,psw:this.nonsense(),hash:this.anonsense()})).then(()=>{
          //登录成功，跳转url
          this.$router.auth = true;
          this.$emit('startCheckLogin');
          this.$router.push(this.$route.query.redirect);
      }).catch((error)=>{
          //登录失败，显示提示消息
          this.trans = 'trans';
          setTimeout(()=>{
            this.$refs.errorMsg.addEventListener('transitionend', this.hideElement);
            this.fadeout = 'fadeout';
          },0);
      });
    },
    selectLang(){
      //切换语言
      this.lang = this.lang=='ENG'?'CHN':'ENG';
    },
    nonsense(){
      let ui = (this.username.length%5);
      let pi = (this.password.length%5);
      let md5 = md.md5.create();
      md5.update(this.username);
      let m = md5.digest().toHex().substr(pi,pi+5);
      md5 = md.md5.create();
      md5.update(this.password);
      let c = md5.digest().toHex().substr(ui,ui+5);
      let s = m+this.password+c;
      let sha256 = md.sha256.create();
      sha256.update(s);
      return sha256.digest().toHex();
    },
    anonsense(){
      let md5 = md.md5.create();
      md5.update(this.username);
      let m = md5.digest().toHex();
      m = m.substr(m.length/2,m.length) + m.substr(0,m.length/2);
      let sha256 = md.sha256.create();
      sha256.update(m);
      m = sha256.digest().toHex();
      let passData = util.hexToBytes(this.stringToHex(this.password));
      let bf = new Blowfish(new Uint8Array(this.hexToArray(m)).buffer);
      return util.bytesToHex(bf.encode(passData));
    },
    stringToHex(str){
      let val="";
　　　for(let i = 0; i < str.length; i++){
　　　　  val += str.charCodeAt(i).toString(16);
　　　}
　　　return val;
    },
    hexToArray(hex){
      hex = hex.replace(/\s|:/gm,'');
      let a = [];
      if (hex.length&1==1) hex="0"+hex;
      for (let i=0;i<hex.length;i+=2) {
          a[i/2] = parseInt(hex.substr(i,2),16);
      }
      return a;
    },
    //form数据转换
    qs(obj){
      var str = "";
      for(var i in obj){
        str+=(i+"=");
        if(obj[i]!=null&&obj[i]!=undefined){
          str+=obj[i];
        }
        str+='&';
      }
      return str.substr(0,str.length-1);
    },
    hideElement() {
      this.$refs.errorMsg.removeEventListener('transitionend', this.hideElement);
      this.trans = '';
      this.fadeout = '';
    }
  },
  watch:{
    //用户名为空时，边框变红
    username(val){
      if(val==''){
        this.userError = true;
      }else{
        this.userError = false;
      }
    },
    password(val){
      if(val==''){
        this.pwdError = true;
      }else{
        this.pwdError = false;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.wrap
{
    width:100%;
    height:340px;
    position:absolute;
    top:50%;
    margin:-260px 0 0 0;
}
@media (min-width: 768px) {
    .login-form
    {
        width:580px;
        margin:0 auto;
        height:100%;
        padding:30px; 
        font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;   
    }
}
@media (max-width: 768px) {
    .login-form
    {
        width:100%;
        height:100%;
        padding:20px; 
        font-family: "Helvetica Neue",Helvetica,Arial,sans-serif; 
    }
}
.row > div{
  margin-bottom:20px;
}
.input-group{
  position: relative;
  box-sizing: border-box;
  display: table;
  border-collapse: separate;
}
.input-group-addon:first-child {
    border-right: 0;
}
.input-group-addon {
    display: table-cell;
    box-sizing: border-box;
    border-collapse: separate;
    background-color: transparent;
    border-radius: 6px;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    padding: 6px 12px;
    font-size: 14px;
    font-weight: normal;
    line-height: 1;
    text-align: center;
    border: 1px solid #ccc;
    width: 1%;
    white-space: nowrap;
    vertical-align: middle;
}
.login-form-control {
    display: table-cell;
    box-sizing: border-box;
    border-collapse: separate;
    height: 60px;
    padding: 10px 30px 10px 0;
    color: #000;
    width: 100%;
    margin-bottom: 0;
    vertical-align: middle;
    background-color: #fff;
    border: 1px solid #ccc;
    border-left: none;
    border-radius: 6px;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
    font-size: 18px;
    line-height: 1.33;
    background-image: none;
}
.login-form-control:focus {
  outline: none;
}
.login-form .icon {
    width:18px;
    height:18px;
    background-size:contain;
    background-repeat:no-repeat;
    margin:0 8px;
}
.login-form .icon-user {
    background-image:url(../assets/user.png);
}
.login-form .icon-pwd {
    background-image:url(../assets/pwd.png);
}
.login-form .icon-clear {
    position:absolute;
    right:10px;
    top:20px;
    cursor:pointer;
    background-image:url(../assets/clear.png);
}
.login-form .icon-clear:hover {
    background-image:url(../assets/clear_hover.png);
}
.login-btn{
  width:100%;
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #486a9a;
  border: none;
  color: #ffffff;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  user-select: none;
  padding: 14px 15px;
  font-size: 18px;
  border-radius: 6px;
}
.lang-select{
  position:absolute;
  top:10px;
  right:20px;
  text-align:right;
  cursor:pointer;
  font-size:16px;
  color:#666666;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;  
  &:hover{
    color:#20a0ff;
  }
}
.c-lang{
  color:#000000;
  font-size:24px;
}
.login-failed{
  position:absolute;
  width:100%;
  font-size:24px;
  text-align:center;
  top:20px;
  color:rgb(173, 33, 33);
  display:none;
  opacity:1;
}
.trans{
  display:block;
  transition:opacity ease-out 2s;
}
.fadeout{
  opacity:0;
}
</style>



