<template>
  <div :class="['col-xs-12','col-sm-6','col-md-4','col-lg-3','oi-col']">
    <div @click="clickHandler" :style="{'background-color':bgc}" class="overview-item">   
      <div class="oi-title">{{props.bay_name}} - {{props.bay_code}}</div>
      <div class="oi-ebtn" :style="{'background-color':props.bay_esd?'#00b050':'#ff0000'}">{{lzn.lbl_esd}}</div>
      <div class="oi-footer">
        <p>{{lzn.lbl_tnkrID}} : {{props.bay_tnkr_code||'N/A'}}</p>
        <p>{{lzn.lbl_loadID}} : {{props.bay_load_id||'N/A'}}</p>
      </div>
      <img :src="img" class="typeimg" v-show="typePicShow"></img>
    </div>
  </div>
</template>

<script>
import Railcar1 from '../assets/Railcar1.png'
import Railcar2 from '../assets/Railcar2.png'
import Truck1 from '../assets/Truck1.png'
import Truck2 from '../assets/Truck2.png'
import Ship1 from '../assets/Ship1.png'
import Ship2 from '../assets/Ship2.png'
import {getInterlocks} from '@/utils'

export default {
  name: 'overviewItem',
  props: {
    props: Object,//发油台数据
    lzn: Object
  },
  data () {
    return {
      interlocks:undefined
    }
  },
  created(){
    this.interlocks = getInterlocks(this.props.bay_code,bayViewConfig.interlockCfg);
  },
  computed:{
    typePicShow(){
      //无配置或配置的interlocks数组长度为0
      if(this.interlocks==undefined){
        return false;
      }
      /*根据interlocks配置，取出bay_interlocks对应的值，
        全为true(联锁联上了)返回true，否则返回false*/
      let l = this.interlocks.length;
      for(let i=0;i<l;i++){
        let interlock = this.props.bay_interlocks[this.interlocks[i].value];
        if(!interlock&&interlock!==undefined){
          return false;
        }
      }
      return true;
    },
    //根据发油台类型返回相应的背景颜色
    bgc(){
      let t = this.props.bay_type_code.toLowerCase();
      if(t=='b_truck'||t=='t_truck'){
        return bayViewConfig.bgc.truck;
      }else if(t=='train'){
        return bayViewConfig.bgc.train;
      }else if(t=='ship'){
        return bayViewConfig.bgc.ship;
      }else{
        return '';
      }     
    },
    //根据发油台类型返回相应的图片
    img(){
      if(this.props.bay_type_code=='B_TRUCK'||this.props.bay_type_code=='T_TRUCK'){
        if(this.props.bay_load_id){
          return Truck1;
        }else{
          return Truck2;
        }
      }
      if(this.props.bay_type_code=='TRAIN'){
        if(this.props.bay_load_id){
          return Railcar1;
        }else{
          return Railcar2;
        }
      }
      if(this.props.bay_type_code=='SHIP'){
        if(this.props.bay_load_id){
          return Ship1;
        }else{
          return Ship2;
        }
      }
    }
  },
  methods:{
    clickHandler(){
      this.$emit('click',this.props);
    }
  }
}
</script>

<style lang="scss">
  .overview-item{
    position:relative;
    box-sizing:border-box;
    width:100%;
    height:250px;
    border-radius:10px;
    padding:10px;
    border:1px solid #424242;
    box-shadow:1px 1px 2px rgba(0,0,0,.4);
  }
  .oi-title{
    text-align:center;
  }
  .oi-ebtn{
    width:250px;
    padding:5px;
    margin:0 auto;
    margin-top:12px;
    background-color:#00b050;
    text-align:center;
    border:1px solid #424242;
    border-radius:10px;
  }
  .oi-footer{
    position:absolute;
    bottom:10px;
    p{
      margin:10px 0;
    }
  }
  .typeimg{
    width:120px;
    position:absolute;
    bottom:10px;
    right:20px;
  }
  .oi-col{
    padding-top:10px;
    padding-bottom:10px;
  }
</style>



