exports.getInterlocks = function (bayCode,interlockCfg) {
  let r;
  let t = interlockCfg.find((j)=>{
    return bayCode==j.bay_code;
  });
  if(t&&t.bay_interlocks){
    //如果bay_interlocks数组为空数组，返回undefined
    if(t.bay_interlocks.length>0){
      r = t.bay_interlocks.filter((i)=>{
        return i.flag
      });
    }
  }
  return r;
}




